import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { validate } from 'wallet-address-validator';
import _ from 'lodash';
import Loading from '../Loading';
import './Search.css';

/*
 * The Search component allows a user to search for transactions.
 */
class Search extends Component {
  constructor() {
    super();

    this.state = {
      addressQuery: '',
      isValid: false,
    };

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.exportCSV = this.exportCSV.bind(this);
  }

  handleChange(event) {
    const addressQuery = event.target.value;
    this.setState({ addressQuery });
    const isValid = validate(addressQuery, 'ETH');
    this.setState({ isValid });
  }

  handleSubmit(event) {
    event.preventDefault();
    const { isValid, addressQuery } = this.state;
    const { updateAddress, getTransactions, page } = this.props;

    if (!isValid) return;

    updateAddress(addressQuery);
    getTransactions(addressQuery, page);
  }

  exportCSV() {
    const { memos } = this.props;
    const csv = `transaction,memo\r\n${_.map(memos, (k, v) => `${v},${k}`).join('\r\n')}\r\n`;
    const blob = new Blob(['\ufeff', csv]);
    const url = URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = url;
    link.download = 'memos.csv';
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  }

  render() {
    const { addressQuery, isValid, loading } = this.state;

    return (
      <div className="Search">
        <div>
          <form onSubmit={this.handleSubmit}>
            <span className="Search-icon" />
            <input
              onChange={this.handleChange}
              type="text"
              className="Search-input"
              placeholder="Wallet Address"
              value={addressQuery}
            />

            {loading && (
              <div className="Search-loading">
                <Loading
                  width="12px"
                  height="12px"
                />
              </div>)}
          </form>
        </div>

        <button
          className="Export-button"
          type="submit"
          disabled={!isValid}
          onClick={this.exportCSV}
        >
          {' '}
          Export as CSV
          {' '}
        </button>
      </div>
    );
  }
}

Search.propTypes = {
  updateAddress: PropTypes.func.isRequired,
  getTransactions: PropTypes.func.isRequired,
  page: PropTypes.number.isRequired,
  memos: PropTypes.objectOf(PropTypes.string).isRequired,
};


export default Search;



